export const initialState = {
    basket: [],
    user: null
};

export const getBasketTotal = (basket) => 
    basket?.reduce((amount,item) => item.price + amount, 0);


const reducer = (state, action) => {
    // console.log(action);
    switch (action.type) {

        case "ADD_TO_BASKET":
            return {
                ...state,
                basket: [...state.basket, action.item],
            };

        case "REMOVE_FROM_BASKET":
            const index = state.basket.findIndex(
                (basketItem) => basketItem.id === action.id
            );
            let newBasket = [...state.basket];
 
            if(index >= 0) {
                newBasket.splice(index, 1); //removes 1 item from index, cuts the array
            }
            else {
                console.warn(`Can't remove product ${action.id} as itas not in the basket!`);
            }

            return {
                ...state,
                basket: newBasket
            }
        case "SET_USER":
            return {
                ...state,
                user: action.user
            }   
         
        default:
            return state;
    }
};

export default reducer; 